import React from 'react';
import Confirm from 'react-confirm-bootstrap';


const DisplayNumbers = ({list, eventHandler}) => {
    return (
        <div>
            <h2>Numerot</h2>
            <ul>
                {list.map(persons => <li className="note" key={persons.id}>
                {persons.name} {persons.number}
                <Confirm
                        onConfirm={eventHandler(persons.id)}
                        body={`Poistetaanko henkil ${persons.name}?`}
                        confirmText="Poista"
                        cancelText="Peruuta"
                        title="Poistovalikko 
                        (my ei savossa mithn hiton ikkunoita tai akkunoita kythet!)">
                        <button id="removeButton">
                        Halluutko sie poistaa thn numeron?</button>
                </Confirm>
                </li>)}
            </ul>
        </div>
    )
}

export default DisplayNumbers;


